#addproduct, #login {
    width: 400px;
    border: 1px solid transparent;
    padding: 0px 15px 15px 15px;
    border-radius: 5px;
    font-family: arial;
    line-height: 16px;
    color: white;
    font-size: 14px;
    background: transparent;
    margin: 100px auto;
  }
  
  form label, form input {
    display: block;
    
    width: 90%
  }
  
  form input {
    padding: 10px;
    border: solid 1px #BDC7D8;
  
  }
  
  .button {
    background-color: #00BFFF;
    /* border-color: #3ac162; */
    font-weight: bold;
    padding: 12px 15px;
    color: #ffffff;
  }
  
  .errorMsg {
    color: #cc0000;
    margin-bottom: 12px;
  }
  
  .sucessMsg {
    color: #6B8E23;
    margin-bottom: 10px;
  }

  .ab{
      margin-top: 10px;
  }